@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
/********** GENERAL **********/


*,
:before,
:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #FFFFFF;
  font-family: 'Poppins', sans-serif;
}

/********** HEADER **********/

.App-header {
  background-color: #5E75FB;
  color: #FFFFFF;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
  font-family: 'Poppins', sans-serif;
}

.main-info {
  margin-left: 15px;
}

nav {
  margin-right: 15px;
  font-size: 22px;
}

ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

li:nth-child(1n) {
  padding-left: 15px;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}
